* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html, body {
  background-color: #1b1b1b;
  font-family: 'Quicksand',sans-serif;
  color: #d1d1d1;
}

.App {
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 8px 12px;
}

.block {
  display: flex;
  justify-content: center;
  padding: 8px;
  border-radius: 8px;
  background-color: #252525;
  box-shadow: 0 2px 5px 1px rgba(0,0,0,.208);
}